.main-bg {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -28px;
  background: url("../img/bg-intro-triangle-mobile.svg"), url("../img/bg-back-mobile@1x.jpg");
  background-size: auto;
  background-repeat: no-repeat;
  background-position: bottom;
  min-width: 320px;
  min-height: 390px;
}

.main-bg_big {
  display: none;
}

@media (min-resolution: @retina-dppx) {
  background-image: url("../img/bg-back-mobile@2x.jpg");
  background-size: auto;
}

@media (min-width: @tablet-width) {
  .main-bg {
    margin-top: 0;
    background: url("../img/bg-intro-triangle-tablet.svg"), url("../img/bg-back-tablet@1x.jpg");
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: bottom;
    min-width: 450px;
    min-height: 519px;
    position: relative;
  }

  @media (min-resolution: @retina-dppx) {
    background-image: url("../img/bg-back-tablet@2x.jpg");
    background-size: 100%;
  }

  .main-bg_big-m {
    display: none;
  }
  .main-bg_big {
    display: inline;
    position: relative;
    top: -7px;
    left: 0;
  }

  // .main-bg_big {
