  //   // height: 518px;
  // }

  // .main-bg_small {
  //   width: 360px;
  //   height: 460px;
  //   // width: 463px;
  //   // height: 289px;
  // }
}

@media (min-width: @desktop-width) {
  .main-bg {
    background-image: url("../img/bg-intro-triangle-desktop.svg"), url("../img/bg-back-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: bottom;
  }

  @media (min-resolution: @retina-dppx) {
    background-image: url("../img/bg-back-desktop@2x.jpg");
    background-size: 100%;
  }

  .main-bg_small {
    height: 301px;
  }
}
